body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul,li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.navbox {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 66;
}
.nav {
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.nav li {
  padding: 2px 10px;
  color: #fff;
}
.nav li a {
  text-decoration: none;
  color: #fff;
}
.nav li:hover {
  color: red;
  cursor: pointer;
}
.nav-line {
  position: absolute;
  height: 0;
  width: 32px;
  border-bottom: 1px solid #fff;;
  top: 55px;
  left: 0;
  z-index: 2;
}
.pingyi {
  transition: all .2s ease-in;
}
.title {
  font-size: 30px;
  position: relative;
  top: 600px;
  text-align: center;
  color: green;
}
ol, ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.slidebox, .slidebox ul, .slidebox ul li{
  width: 100%;
  height: 500px;
  min-width: 980px;
  position: absolute;
  top: 0;
  left: 0;
}
.slidebox ul li:nth-child(1) {
  background: url(../../static/media/1.8bc08be9.jpg) top center no-repeat;
}
.slidebox ul li:nth-child(2) {
  background: url(../../static/media/2.aa09190e.jpg) top center no-repeat;
}
.slidebox ul li:nth-child(3) {
  background: url(../../static/media/3.2827e02b.jpg) top center no-repeat;
}
.slidebox ul li:nth-child(4) {
  background: url(../../static/media/4.b029e4ef.jpg) top center no-repeat;
}

.slidebox ol {
  width: 100%;
  height: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 6;
}
.slidebox ol li {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer;
}
.active {
  z-index: 1;
  -webkit-animation: opt 0.2s ease-in;
          animation: opt 0.2s ease-in;
}
.red {
  background-color: #ff6460;
}
.white {
  background-color: #fff;
}
@-webkit-keyframes opt {
  0% {
    opacity: .9;
  }
  100% {
    opacity: 1;
  }
}


.banner {
  width: 980px;
  height: 175px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.banner_li {
  width: 25%;
  position: relative;
}
.li_bg {
  width: 150px;
  height: 175px;
  margin: 0 auto;
  background-size: 100% 100%;
  -webkit-transform: rotateZ(0);
          transform: rotateZ(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
}
.li_bg.active {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
.li_title {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-180%);
          transform: translate(-50%,-180%)
}
.li_img {
  display: block;
  width: 80px;
  height: 65px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-20%);
          transform: translate(-50%,-20%)
}
.li_img.act {
  -webkit-transform: translate(-30%,5%) rotate(-45deg);
          transform: translate(-30%,5%) rotate(-45deg);
  width: 120px;
  height: 100px;
}

.App {
  text-align: center;
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
  width: 980px;
  position: relative;
  top: 510px;
  margin: 0 auto;
  background: #fff;
}
.big_title {
  width: 980px;
  font-size: 20px;
  text-align: center;
  line-height: 60px;
  margin-top: 20px;
  position: relative;
}
.big_title span{
  display: inline-block;
  padding: 0 10px;
}
.big_title::before {
  content: '';
  width: 43%;
  height: 1px;
  background-color: #666;
  position: absolute;
  left: 0;
  top: 30px;
}
.big_title::after {
  content: '';
  width: 43%;
  height: 1px;
  background-color: #666;
  position: absolute;
  right: 0;
  top: 30px;
}
.row {
  width: 980px;
  display: flex;
  justify-content: center;
  height: 290px;
}
.row img {
  width: 480px;
  height: 290px;
}
.video {
  display: block;
  object-fit: fill;
  width: 500px;
  height: 290px;
}
